import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import "./generic.css";
import LoginNavBar from "./components/loginNavBar";

export default function Register() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [name, setName] = useState("");
    const [userType, setUserType] = useState("");
    let loggedInUser = useState("");

    function handleSubmit() {

    }

    return (
        <div className="Auth">
            <LoginNavBar user={loggedInUser}></LoginNavBar>
            <Form onSubmit={handleSubmit}>
                <h4> Please register below</h4>
                <Form.Group size="lg" controlId="name">
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                        autoFocus
                        type="name"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                    />
                </Form.Group>
                <Form.Group size="lg" controlId="email">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                        autoFocus
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                </Form.Group>
                <Form.Group size="lg" controlId="password">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                </Form.Group>
                <Form.Group size="lg" controlId="userType">
                    <Form.Label>User Type</Form.Label>
                    <Form.Control as="select" defaultValue="User" onChange={(e) => setUserType(e.target.value)}>
                        <option value={userType}>User</option>
                        <option value={userType}>Mechanic</option>
                    </Form.Control>
                </Form.Group>
                <Button block size="lg" type="submit">
                    Register
        </Button>
            </Form>
        </div>
    );
}